import firebase from 'firebase/app';
import { auth } from './firebase';

export const checkAuthState = (callback) => { // 
  auth.onAuthStateChanged((user) => {
    callback(user);
  });
};

export const signOut = () => {
  auth.signOut();
  console.log('');
};

// eslint-disable-next-line max-len
export const signUp = async (email, password) => new Promise((resolve, reject) => {
  auth.createUserWithEmailAndPassword(email, password)
    .then((result) => {
      resolve(result);
    })
    .catch((err) => {
      reject(err);
    });
});

export const signIn = async (email, password) => new Promise((resolve, reject) => {
  auth.signInWithEmailAndPassword(email, password)
    .then((result) => {
      resolve(result);
    })
    .catch((err) => {
      reject(err);
    });
});

// callback
export const loginBySocialThen = async (social) => {
  const brand = () => ({
    google: () => new firebase.auth.GoogleAuthProvider(),
    facebook: () => new firebase.auth.FacebookAuthProvider(),
    twitter: () => new firebase.auth.TwitterAuthProvider(),
  });

  const provider = brand()[social]();

  return new Promise((resolve, reject) => {
    auth.signInWithPopup(provider)
      .then((result) => {
        resolve(result);
      })
      .catch((err) => { // errorcode, message, email, credential
        reject(err);
      });
  });
};
